
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XAddress from 'vux/src/components/x-address/index.vue'

import ViewBox from '../../node_modules/vux/src/components/view-box/index.vue'

export default {

directives: {
TransferDom
},
components: {
ViewBox,
XHeader,
XButton,
Box,
XInput,
Group,
Cell,
Toast,
Tabbar,
TabbarItem,
Flexbox,
FlexboxItem,
Divider,
Countup,
Swiper,
SwiperItem,
Confirm,
XSwitch,
Datetime,
Blur,
Tab,
TabItem,
XAddress
},
filters: {
feeFormat (value) {
if (!value) {
return '包邮'
}
}
},
mounted () {
this.userId = window.localStorage.getItem('userId')
this.failureShowToast = false
this.changeSuccessShow = false
this.onlyGetAddress()
this.$vux.loading.show({
text: 'loading'
})
this.getGift()
},
data () {
return {
title: '兑换',
giftTitle: '工程师开小差了！',
realName: '',
phone: '',
addressAll: '',
isNoAddress: false,
originalPrice: 0,
expressFee: 0,
giftIcon: '',
giftScore: 0, // 积分购买
stock: 0,
giftDetail: '',
giftSubtitle: '暂时没有简介',
userId: '',
ownPic: '../assets/dialog/01.jpg',
url: '../assets/dialog/01.jpg',
showConfirmBox: false,
computeDaysValue: '',
index: 0,
picListIndex: 0,
giftPics: [],
picList: [{
url: 'javascript:',
img: 'https://static.vux.li/demo/1.jpg'

}, {
url: 'javascript:',
img: 'https://static.vux.li/demo/2.jpg'

}, {
url: 'javascript:',
img: 'https://static.vux.li/demo/5.jpg',

fallbackImg: 'https://static.vux.li/demo/3.jpg'
}],
mobile: '',
iconType: '',
successText: '',
changeSuccessShow: false,
changeFailureShow: false,
failureText: '',
placeHolderValue: '填写配送地址'
}
},
methods: {
onlyGetAddress () {
this.$http.post(this.$apis.getExpressInfo, {userId: this.userId}).then(res => {
if (res.code === 200) {
this.placeHolderValue = res.data.address
} else {
this.$vux.loading.hide()
}
}).catch(function (error) {
console.log('Error: ' + error)
this.$vux.loading.hide()
})
},
getExpressInfo () {
this.$http.post(this.$apis.getExpressInfo, {userId: this.userId}).then(res => {
if (res.code === 200) {
this.realName = res.data.realName
this.phone = res.data.phone
this.placeHolderValue = res.data.address
this.addressAll = res.data.address + res.data.realAddress + ''
this.$vux.loading.hide()
this.showConfirmBox = true
} else {
this.$vux.loading.hide()
this.isNoAddress = true
}
}).catch(error => {
console.log('Error: ' + error)
this.$vux.loading.hide()
})
},
redeemClick () {
document.body.scrollTop = 0
this.$vux.loading.show({
text: 'loading'
})
this.getExpressInfo()
},
onCancelback () {
},
onPicIndexChange () {

},
goRedeemDetail () {
this.$router.push('setting')
},
goStruction () {
this.$router.push('setting')
},
goQuestion () {
this.showConfirmBox = true
},
onIndexChange (newIndex, oldIndex) {
console.log(newIndex, oldIndex)
},
onCancel () {
},

onConfirm () {
this.$router.push({name: 'Paygift', query: {giftId: this.$route.query.giftId}})
},
goAddAddress () {
this.$router.push('addressInfo')
},
onShow () {
},
onHide () {
},
change (val) {
console.log('on change', val)
},
onBlur (val) {
console.log('on blur', val)
},
onFocus (val, $event) {
console.log('on focus', val, $event)
},
onEnter (val) {
console.log('click enter!', val)
},
getGift () {
let giftId = this.$route.query.giftId
this.$http.post(this.$apis.getGiftById, {giftId: giftId}).then(res => {
if (res.code === 200) {
let picArr = []
let obj = {url: 'javascript:', img: res.data.giftIcon}
picArr.push(obj)
picArr.push(obj)
this.picList = picArr
console.log(picArr)
this.giftTitle = res.data.giftTitle
this.giftPics = res.data.giftPics
this.expressFee = res.data.expressFee
this.giftScore = res.data.giftScore
this.originalPrice = res.data.originalPrice
this.giftDetail = res.data.giftDetail
this.giftIcon = res.data.giftIcon
this.giftSubtitle = res.data.giftSubtitle
this.stock = res.data.stock
this.$vux.loading.hide()
} else {
this.$vux.loading.hide()
}
}).catch(error => {
console.log('Error: ' + error)
this.$vux.loading.hide()
})
}
}

}



